.bottom-modal {
  bottom: 150px;
  right: 50%;
  transform: translateX(-50%);
  width: 95%;
  max-width: 400px;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: var(--box-shadow);
  min-height: 100px;
  padding: var(--spacing-1) var(--spacing-2);
  background: White;
  border-radius: var(--border-radius);
}

