  font-size: 24px;
  font-weight: 600;
  color: var(--color-primary);
}

.bottom-modal-text {
  color: var(--font-faded);
}

.bottom-modal-icon {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: var(--spacing-1);
  flex-shrink: 0;
}

.bottom-modal svg {
  font-size: 32px;
  color: white;
}

@media (min-width: 1025px) {
  .bottom-modal {
    bottom: 20px;
    transform: translateX(0%);
    right: 20px;
    width: auto;
